import React, { useState, useEffect } from "react";
import eximage from '../uploaded.png';
import axios from 'axios';
import Loading from './loading'; //   

const Services = (props) => {
  const [uploadedImage, setUploadedImage] = useState(eximage);
  const [isLoading, setIsLoading] = useState(false); //     
  const [pricePerLiter, setPricePerLiter] = useState('');
  const [carbonTax, setCarbonTax] = useState('');
  const [amount, setAmount] = useState('');
  const [result, setResult] = useState('');
  const [c_result, c_setResult] = useState('');

  //  base64  
  const imageToBase64 = (file) => {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.readAsDataURL(file);
  
      reader.onload = () => {
        // Base64   
        let base64Image = reader.result.split(',')[1];
        
        //  
        const padding = '='.repeat((4 - base64Image.length % 4) % 4);
        base64Image += padding;
  
        resolve(base64Image);
      };
  
      reader.onerror = (error) => {
        reject(error);
      };
    });
  };
  


  const handleImageClick = () => {
    const fileInput = document.getElementById('fileInput');
  
    if (fileInput) {
      fileInput.click();
    }
  };

  const handleCancelImage = () => {
    setUploadedImage(eximage);
  };

  const handleImageUpload = async (event) => {
    setIsLoading(true); //  
  
    const fileInput = document.getElementById('fileInput');
    const file = fileInput.files[0];
    
    if (file) {
      try {
        const base64Image = await imageToBase64(file); //  Base64 
        const token = localStorage.getItem('accessToken'); //   
    
        if (!token) {
          console.error(' .');
          setIsLoading(false); //  
          return;
        }
    
        //     POST 
        const response = await axios.post(`http://34.22.80.43/api/process-image/`, {
          image: base64Image,
        }, {
          headers: {
            Authorization: `Bearer ${token}`, //   
          },
        });
    
        console.log(' :', response.data); //   
    
        setUploadedImage(response.data.second_model_image_url); //   URL  
        setResult(response.data); //      
      } catch (error) {
        console.error(' :', error); //  
      } finally {
        setIsLoading(false); //      
      }
    } else {
      setIsLoading(false); //       
    }
  };
  
  
  
  
//
  const calculate = () => {
    const price = parseFloat(pricePerLiter);
    const tax = parseFloat(carbonTax);
    const quantity = parseFloat(amount);

    if (isNaN(price) || isNaN(tax) || isNaN(quantity)) {
      alert(' .');
      return;
    }

    const totalCost = (price + tax) * quantity;
    c_setResult(` ${totalCost.toFixed(2)}`);
  };

  const resetCalculator = () => {
    setPricePerLiter('');
    setCarbonTax('');
    setAmount('');
    c_setResult('');
  };


  const handleImageChange = (event) => {
    const file = event.target.files[0];

    if (file) {
      try {
        const reader = new FileReader();

        reader.onload = function (e) {
          setUploadedImage(e.target.result);
        };

        reader.readAsDataURL(file);
      } catch (error) {
        console.error(' :', error);
      }
    }
  };


  return (
    <div style={{ padding: '100px' }} className="text-center">
      <div className="container" style={{ height: '700px'}}>
        <div className="section-title"></div>

        <div className="row row-md-6">
          <div className="col-md-6 ">
            <div 
              style={{
                position: 'relative',
                overflow: 'hidden',
                width: '600',
                height: '600',
                maxWidth: '600px',
                maxHeight: '600px',
                minWidth: '200px',
                minHeight: '200px',
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
              }}
            >
              {uploadedImage ? (
                <img
                  src={uploadedImage}
                  alt="   "
                  style={{
                    maxWidth: '100%',
                    maxHeight: '100%',
                    objectFit: 'contain',
                    cursor: 'pointer',
                  }}
                  onClick={handleImageClick}
                />
              ) : (
                <img
                  src={eximage}
                  alt="Example Image"
                  style={{ cursor: 'pointer' }}
                  onClick={handleImageClick}
                />
              )}
              <input
                type="file"
                id="fileInput"
                style={{ display: 'none' }}
                onChange={handleImageChange}
              />
            </div>
        </div>

          {/*   USERID  second_model_image  */}
          <div className="col-md-6 text-left">
            <div
              style={{
                padding: '20px',
              }}
            >
              <h1 className="fw-bolder" style={{fontSize: '35'}}>  </h1>
              <button
              className="btn"
              type="button"
              style={{
                background: '#9ADE7B',
                fontSize: '16px',  //   
                fontWeight: 'bold',  //  
                letterSpacing: '1px',  //   
                padding: '5px 10px',  //   ( 10px,  20px)
              }}
              onClick={handleImageUpload}
            >
               
            </button>
              <button type="button" 
              className="btn btn-dark mx-3 my-3" 
              style={{
                fontSize: '16px',  
                fontWeight: 'bold',  
                letterSpacing: '1px', 
                padding: '5px 10px', 
              }}
              onClick={handleCancelImage}>
                
              </button>
                

                <div className="my-3">
                  {uploadedImage ? (
                    <div key={result.id}>
                      <div>
                        <p>VEHICLE TYPE: {result.vehicle_type}</p>
                        <p>PLATE: {result.license_plate_text}</p>
                        <p>PLATE VEHICLE TYPE: {result.LICENSE_PLATE_VEHICLE_TYPE}</p>
                      </div>
                    </div>
                  ) : null}
                </div>


                <label>L </label>
                <input type="text" className="form-control" value={pricePerLiter} onChange={(e) => setPricePerLiter(e.target.value)} required />
                <label></label>
                <input type="text" className="form-control" value={carbonTax} onChange={(e) => setCarbonTax(e.target.value)} required />
                <label>L</label>
                <input type="text" className="form-control" value={amount} onChange={(e) => setAmount(e.target.value)} required />
                
                <button type="button" className="btn btn-warning my-3" 
                style={{
                  fontSize: '16px',  
                  fontWeight: 'bold',  
                  letterSpacing: '1px', 
                  padding: '5px 10px', 
                }}
                onClick={calculate}>
                  
                </button>
                <button type="button" className="btn btn-dark mx-3" 
                style={{
                  fontSize: '16px',  
                  fontWeight: 'bold',  
                  letterSpacing: '1px', 
                  padding: '5px 10px', 
                }}
                onClick={resetCalculator}></button>
                
                <div className="mt-3">
                  <h5> :</h5>
                  <h1 className="fw-bolder">{c_result}</h1>
                </div>

                
            </div>
          </div>
        </div>
      </div>
      <div style={{ padding: '0px' }} className="text-center">
  {isLoading && <Loading />} {/*   true     */}
  <div className="container" style={{ height: '-20px', marginTop: '-10px' }}> {/*        */}
  </div>
</div>


      <div className="my-3">
        {uploadedImage ? (
          <div key={result.id}>
            <p>{result.g_print}</p>
          </div>
        ) : null}
      </div>
      </div>




  );
  
};

export default Services;