// App.js
import React, {useState} from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Routes, Route, BrowserRouter } from 'react-router-dom';
import FAQ from './components/faq';
import Contact from './components/contact';
import Introduce from './components/introduce';
import Gallery from './components/gallery';
import Services from './components/services';
import Team from './components/Team';
import Navigation from './components/navigation';

import Login from './components/login';
import Signup from './components/signup';
import MyPageB from './components/mypage_b';
import MyPage from './components/mypage';
import Practice from './components/practice';

import { GoogleOAuthProvider } from "@react-oauth/google";

const MainContent = () => {
  const data = {
    //  
  };

  return (
    <div>
      <Introduce data={data.introduce} />
      <Gallery data={data.gallery} />
      <Team data={data.team} />
      <Contact data={data.contact} />
    </div>
  );
};

function App() {
  const [isLoggedIn, setLoggedIn] = useState(false);
  const [isCompanyMember, setCompanyMember] = useState(false);

  const handleLogin = () => {
    //    .
    setLoggedIn(true);

    const isCompanyMember = /*    companyMember   */

    setCompanyMember(isCompanyMember);
    //      isCompanyMember .
    // setCompanyMember(true)  setCompanyMember(false)
  };

  const handleLogout = () => {
    //    .
    setLoggedIn(false);
  };

  return (
    <GoogleOAuthProvider clientId={process.env.REACT_APP_GOOGLE_AUTH_CLIENT_ID}
      onScriptLoadError={() => console.log("")}
      onScriptLoadSuccess={() => console.log("")}>
      <BrowserRouter>
        <div>
        <Navigation isLoggedIn={isLoggedIn} isCompanyMember={isCompanyMember} handleLogout={handleLogout} />
          <Routes>
            <Route path="/" element={<MainContent />} />
            <Route path="/faq" element={<FAQ />} />
            <Route path="/services" element={<Services />} />
            <Route path="/practice" element={<Practice />} />
            <Route
              path="/login"
              element={<Login handleLogin={handleLogin} />}
            />
            <Route path="/signup" element={<Signup />} />
            <Route path="/mypage_b" element={<MyPageB />} />
            <Route path="/mypage" element={<MyPage />} />

          </Routes>
        </div>
      </BrowserRouter>
    </GoogleOAuthProvider>
  );
  };

export default App;
