import React, { useState, useRef, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import 'bootstrap/dist/css/bootstrap.min.css';
import eximage from '../personicon.png';
import axios from 'axios';

import Mypage_option3 from './mypage_option3'

function getCarType(carcode) {
  switch (carcode) {
    case 0:
    case 1:
      return "";
    case 2:
    case 3:
      return "";
    case 4:
      return "";
    case 5:
      return "";
    case 6:
      return "";
    default:
      return ""; 
  }
}

const MyPage = () => {
  const [selectedSection, setSelectedSection] = useState('option1');
  const [uploadedImage, setUploadedImage] = useState(null);
  const fileInputRef = useRef(null);
  const [editableData, setEditableData] = useState([]);
  const navigate = useNavigate();
  const [isEditMode, setIsEditMode] = useState(false);
  const [username, setUsername] = useState(localStorage.getItem('username') || null);
  const updatedUserData = {};
  const [fuelData, setFuelData] = useState(null);

  const [latestImageResults, setLatestImageResults] = useState(null);

  useEffect(() => {
    const fetchUserData = async () => {
      try {
        const token = localStorage.getItem('accessToken');
        const response = await axios.get(`http://34.22.80.43/users/${username}/`, {
          headers: {
            'Authorization': `Bearer ${token}`,
          },
        });
        const userData = response.data;
        setUsername(userData.username);

        setEditableData([
          { title: '', content: userData.username },
          { title: '', content: userData.email },
          { title: '', content: userData.phone },
          { title: '', content: userData.sex },
          { title: ' ', content: userData.password },
        ]);
      } catch (error) {
        console.error('    :', error);
      }
    };

    fetchUserData();
  }, [username]);

  const handleSectionClick = (sectionId) => {
    setSelectedSection(sectionId);
    setIsEditMode(false);
  };

  const handleInputChange = (index, value) => {
    const updatedData = [...editableData];
    updatedData[index].content = value;
    setEditableData(updatedData);
  };

  const fetchLatestImageResult = async () => {
    try {
      const token = localStorage.getItem('accessToken');
      if (!token) {
        console.error(' .');
        return;
      }

      const config = {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      };

      const response = await axios.get(`http://34.22.80.43/api/process-image2/`, config);

      console.log('  (process-image2):', response.data);

      const responseData = response.data;
      setLatestImageResults(responseData);
    } catch (error) {
      console.error(' :', error);
    }
  };


  const handleSave = async () => {
    try {

      editableData.forEach(item => {
        const lowercaseTitle = item.title.toLowerCase();
    
        if (lowercaseTitle === ' ') {
          updatedUserData['password'] = item.content;
        } else if (lowercaseTitle === '') {
          updatedUserData['username'] = item.content;
        } else if (lowercaseTitle === '') {
          updatedUserData['email'] = item.content;
        } else if (lowercaseTitle === '') {
          updatedUserData['phone'] = item.content;
        } else if (lowercaseTitle === '') {
          updatedUserData['sex'] = item.content;
        }
      });

      const token = localStorage.getItem('accessToken');
      const passwordContent = editableData.find(item => item.title.toLowerCase() === ' ').content;
      const userDataPassword = editableData.find(item => item.title.toLowerCase() === ' ').content;
  
      if (passwordContent !== userDataPassword) {
        alert('  .   .');
        return;
      }
  
      //   .
      const requestData = {
        password: passwordContent,
        ...updatedUserData,
      };
  
      //     PUT  .
      const response = await axios.put(
        `http://34.22.80.43/users/${username}/`,
        requestData,
        {
          headers: {
            'Authorization': `Bearer ${token}`,
          },
        }
      );
  
      if (response.status === 200) {
        alert('  .');
        setIsEditMode(false);
      } else {
        console.error('  :', response);
        alert('  .');
      }
    } catch (error) {
      console.error('   :', error);
      alert('  .');
    }
  };


  useEffect(() => {
    const fetchFuelData = async () => {
      try {
        const token = localStorage.getItem('accessToken');
        
        // Fetch address data
        const config = {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        };
  
        // Fetch fuel data between formattedStartDate and formattedEndDate
        const response = await axios.get(`http://34.22.80.43/api/image-with-text/`, config);
  
        if (response.data) {
          const fuelDataArray = response.data;
  
          // Check if there is any fuel data
          if (fuelDataArray.length > 0) {
            // Take the latest fuel data
            const firstfuel = fuelDataArray[fuelDataArray.length - 1];
            setFuelData(firstfuel);
            console.log('  ', firstfuel);
          } else {
            console.warn('FUEL_DATA .');
          }
        } else {
          console.error('response.data  FUEL_DATA  .');
        }
      } catch (error) {
        console.error('    :', error);
  
        if (axios.isAxiosError(error)) {
          console.error('AxiosError  :', error.response.data);
        }
      }
    };
  
    // fetchFuelData  
    fetchFuelData();
  }, [setFuelData]);
  



  const linkStyle = {
    textDecorationLine: 'none',
    color: 'black',
    padding: '10px',
    cursor: 'pointer',
  };

 //   
  const handleImageUpload = (event) => {
    const file = event.target.files[0];
    if (file) {
      const reader = new FileReader();
      reader.onloadend = () => {
        setUploadedImage(reader.result);
      };
      reader.readAsDataURL(file);
    }
  };

   //     
  const handleImageClick = () => {
    if (fileInputRef.current) {
      fileInputRef.current.click();
    }
  };

    //   
  const handleCancelImage = () => {
    setUploadedImage(null);
  }

  const handleConfirm = () => {

    const deleteUserAccount = async () => {
      try {
        const accessToken = localStorage.getItem('accessToken');

        if (!accessToken) {
          // Access Token     
          alert(' .');
          return;
        }

        const response = await axios.delete(`http://34.22.80.43/users/${username}/`, {
          headers: {
            'Authorization': `Bearer ${accessToken}`,
          },
        });

        if (response.status === 204) {
          //   
          alert(' .');
          //  
          localStorage.removeItem('isLoggedIn');
          localStorage.removeItem('accessToken');
          localStorage.removeItem('refreshToken');
          //  
          navigate('/');
        } else {
          //   
          console.error(' :', response);
          alert(' .');
        }
      } catch (error) {
        //  
        console.error('  :', error);
        alert(' .');
      }
    };

    //    
    deleteUserAccount();
  };


    //    ... 
  // const cardata = [
  //   { title: 'VEHICLE TYPE', content: '' },
  //   { title: '', content: '' },
  //   { title: 'CO2 (g/km)', content: '' },
  //   { title: '', content: '' },
  //   { title: ' ', content: 'asd' },
  // ];


  return (
    <div>
      <div style={{ padding: '100px' }}>
        <div className="col-md-3">
          <div
            className="border-end"
            style={{
              width: '200px',
              height: '100%',
              display: 'flex',
              justifyContent: 'center',
              background: '#d9d9d9',
            }}
          >
            {/*  */}
            <ul className="nav navbar-nav ml-auto" style={{ listStyle: 'none', padding: 0 }}>
              <h1 className="m-5"></h1>
              {['option1', 'option2', 'option3'].map((section) => (
                <li key={section}>
                  <div
                    onClick={() => handleSectionClick(section)}
                    className={`my-page-link ${selectedSection === section ? 'active' : ''}`}
                    style={{
                      ...linkStyle,
                      backgroundColor: selectedSection === section ? '#f6f6f6' : '',
                    }}
                  >
                    {/*    */}
                    <h4>{section === 'option1' ? '  ' : section === 'option2' ? ' ' : section === 'option3' ? '': ''}</h4>
                  </div>
                </li>
              ))}
            </ul>
          </div>
        </div>

        {/*  */}
        <div className="col-md-9 row-md-3">
          <div id="option1" style={{ height: '10%', width: '100%', background: '#E5F4D2', display: 'flex', alignItems: 'center' }}>
            <div style={{ width: '80px', height: '70px', overflow: 'hidden', borderRadius: '50%', margin: '10px' }}>
              {uploadedImage && (
                <img
                  src={uploadedImage}
                  onClick={handleImageClick}
                  alt="Uploaded Preview"
                  style={{ width: '100%', height: '100%', objectFit: 'cover' }}
                />
              )}
  
              {!uploadedImage && (
                <>
                  <input type="file" onChange={handleImageUpload} ref={fileInputRef} style={{ display: 'none' }} />
                  <img
                    src={eximage}
                    alt="Example Image"
                    style={{ width: '100%', height: '100%', borderRadius: '50%', cursor: 'pointer', objectFit: 'cover' }}
                    onClick={handleImageClick}
                  />
                </>
              )}
            </div>
            <div style={{ padding: '0px', width: '100%' }}>
              <div className="fs-1" style={{ display: 'flex', alignItems: 'center' }}>
              <span><strong>{username}</strong></span> <span> .</span>
              </div>
            </div>
          </div>
        </div>

        {/*    */}
        <div className="col-md-8 row-md-10">
          {selectedSection === 'option1' && (
            <div id="option1" style={{ height: '50%', background: '#ffffff' }}>
              <div className="col-md-3">
                <div style={{
                  border: uploadedImage ? 'none' : '2px dashed #ccc',
                  borderRadius: '5px',
                  padding: '10px',
                  marginBottom: '10px',
                  position: 'relative',
                  display: 'flex',
                  justifyContent: 'center',
                  alignItems: 'center',
                  height: '400px',
                  width: '400px',
                  marginTop: '20px',
                }}>
                  {uploadedImage && (
                    <div style={{ position: 'relative', textAlign: 'center' }}>
                      <img
                        src={uploadedImage}
                        alt="Uploaded"
                        style={{ width: '100%', height: 'auto', objectFit: 'contain', marginBottom: '10px' }}
                      />
                      <div style={{ marginTop: '10px' }}>
                        <button type="button" className="btn btn-dark" onClick={handleCancelImage}>
                           
                        </button>
                      </div>
                    </div>
                  )}

                  {/*   */}
                  {!uploadedImage && (
                    <>
                      <input
                        type="file"
                        onChange={handleImageUpload}
                        ref={fileInputRef}
                        style={{ display: 'none' }}
                      />
                      <div
                        onClick={handleImageClick}
                        style={{
                          cursor: 'pointer',
                          color: '#ccc',
                          fontSize: '16px',
                        }}
                      >
                        Click to upload image
                      </div>
                    </>
                  )}
                </div>
              </div>

              {/* ,   */}
              <div className="col-sm-6">
                <div className="h4" style={{ position: 'relative', top: '20px', left: '48%', width: '100%' }}>
                {fuelData  && (
                  <div key={fuelData.id} className="col-sm-12 border-bottom p-3 mx-5 mt-3" style={{ padding: '15px', fontSize: '15px' }}>
                    <div>
                      <label>:</label> <span>{fuelData.vehicle_type}</span>
                    </div>
                    <div>
                      <label>:</label> <span>{fuelData.g_print}</span>
                    </div>
                  </div>
                  )}
                </div>
              </div>
            </div>
          )}

                {/* {cardata.map((data, index) => (
                    <div key={index} className="col-sm-12 border-bottom p-3 mx-5 mt-3" style={{ padding: '15px', fontSize: '15px' }}>
                    <dt className="col-sm-6">{data.title}</dt>
                    <dd className="col-sm-6">
                    {data.content}
                    </dd>
                    </div>
                ))}
                 */}
            {selectedSection === 'option2' && (
              <div id="option2" style={{ height: '20px' }}>
                <dl className="row">
                  {editableData.map((item, index) => (
                    <div key={index} className="col-sm-12 border-bottom" style={{ padding: '15px', fontSize: '15px' }}>
                      <dt className="col-sm-3">{item.title}</dt>
                      <dd className="col-sm-9">
                        {item.title.toLowerCase() === '' ? (
                          <span>{item.content}</span>
                        ) : (
                          isEditMode ? (
                            <input
                              type="text"
                              value={item.content}
                              onChange={(e) => handleInputChange(index, e.target.value)}
                            />
                          ) : (
                            item.content
                          )
                        )}
                      </dd>
                    </div>
                  ))}
                </dl>
                {isEditMode ? (
                  <>
                    <button className="btn btn-primary mx-3" style={{ fontSize: '16px' }} onClick={handleSave}>
                      
                    </button>
                  </>
                ) : (
                  <button className="btn btn-primary mx-3" style={{ fontSize: '16px' }} onClick={() => setIsEditMode(true)}>
                    
                  </button>
                )}
                <h1 className="btn btn-danger" style={{fontSize: '16px'}} onClick={(handleConfirm)}>
                  
                </h1>
              </div>
            )}





            {selectedSection === 'option3' && (
              <Mypage_option3 />
            )}


        </div>
      </div>
    </div>
  );
};

export default MyPage;